@import url(https://fonts.googleapis.com/css?family=Roboto:100);
.abContainer {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}

.abContainer .sns {
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 768px) {
}

.movies_movie {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100%;
  box-shadow: 0 10px 12px rgba(50, 50, 93, 0.11),
    0 4px 8px rgba(50, 50, 93, 0.11);
  border-radius: 5px;
}
.movies_movie .movies_data {
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  color: white;
  margin-top: 5px;
}
.movies_movie .movies_data .movie_title {
  font-size: 15px;
}

.movies_movie .movies_data .movie_year {
  font-size: 10px;
}
.movies_movie ul {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  padding-left: 0;
  font-size: 4px;
}
@media screen and (max-width: 768px) {
  .movies_movie {
    display: flex;
    flex-direction: column;
    width: 80;
  }
}

@keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  height: 100%;
}
html,
body,
#root,
.container {
  height: 100%;
}

.loader {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font: 2em "Roboto", sans-serif;
  padding-left: 0px;
  margin-top: 50px;
}

.loader .loader_img {
  display: inline-block;
  height: 50px;
  width: 20%;
  border: 4px solid rgba(255, 255, 255, 0.171);
  border-radius: 50%;
  border-top-color: black;
  animation: spin 1s ease-in-out infinite;
  -webkit-animation: spin 1s ease-in-out infinite;
}

.movies {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
  flex-wrap: wrap;
}

.movies a {
  background-color: black;
  width: 20%;
  height: 100%;
  border: 1px solid white;
  text-decoration: none;
  margin: 7px auto;
  border-radius: 5px;
  box-shadow: 0 10px 12px rgba(50, 50, 93, 0.11),
    0 4px 8px rgba(50, 50, 93, 0.11);
}

.movies a:hover {
  transform: scale(1.1);
}

@media screen and (max-width: 768px) {
  .movies {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 30px;
  }
  .movies a {
    width: 80%;
  }
}

.navContainer {
  width: 100%;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  z-index: 1;
  padding: 10px auto;
  box-shadow: 20px 25px 30px rgba(50, 50, 93, 0.11);
}

.navContainer span {
  background-color: black;
  color: white;
  padding: 10px 10px;
  text-decoration: none;
}

.navContainer span:hover {
  transition: 0.3s all ease-in-out;
  background-color: gray;
}

.navContainer a {
  text-decoration: none;
}

.dtContainer {
  width: 100%;
  height: 100%;
  display: flex;
  margin-top: 30px;
}

.dtData {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.dtContainer .dtData .dtTitle {
  text-align: center;
}

.dtContainer .dtData ul {
  width: 50%;
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding-left: 0px;
  font-weight: 600;
}

.dtContainer .dtData .dtSummary {
  font-size: 20px;
  margin: 30px 150px;
  text-align: center;
}

@media screen and (max-width: 768px) {
  .dtContainer {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 30px;
  }

  .dtContainer .dtImg {
    width: 100%;
    height: 100%;
  }

  .dtContainer .dtData .dtTitle {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 100px;
    text-align: center;
  }

  .dtContainer .dtData .dtYear {
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    margin-top: 100px;
    text-align: center;
  }

  .dtContainer .dtData ul {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    list-style: none;
    font-weight: 600;
    margin-top: 100px;
    margin-bottom: 100px;
  }

  .dtContainer .dtData .dtSummary {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;

    margin-top: 200px;
  }

  .dtContainer .dtData .dtSummary .dtp {
    padding-top: 100px;
  }
}

